import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { validateSchema } from '../../../utils/ValidationHelper'
import { createUpdatePermissionSchema } from '../../../utils/ValidationSchema'
import { createShortName } from '../../../utils/common'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { CustomButton, CustomError } from '../../globalComponents'
import CustomSelect from '../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../translation'

const CreateUpdatePermission = (props) => {
    const { t } = useCustomTranslation()
    const { createPermissionPending } = useSelector(state => state?.permission)
    const {
        openModel,
        handleOpenClose,
        handleCreateUpdatePermission,
        menuPermissionData,
        permissionConstantData

    } = props
    const [errors, setErrors] = useState({})
    const [createUpdatePermissionFormData, setCreateUpdatePermissionFormData] = useState({
        route_key: '',
        routes: '',
        permission_type: null,
        title: '',
        parent: null,
        sort_order: '',
        icon: '',
        sub_title: '',
        category: null
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };
    
        setCreateUpdatePermissionFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdatePermission = async () => {
        const validationResponse = await validateSchema(createUpdatePermissionSchema, createUpdatePermissionFormData);
        if (validationResponse.isValid) {
            const {
                route_key,
                routes,
                permission_type,
                title,
                parent,
                sort_order,
                icon,
                sub_title,
                category
            } = createUpdatePermissionFormData
            const params = {
                route_key,
                routes,
                permission_type:permission_type?.value,
                title,
                parent:parent?.value,
                sort_order,
                icon,
                sub_title,
                category:category?.value
            }
            handleCreateUpdatePermission(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    const resetForm = () => {
        setCreateUpdatePermissionFormData({
            route_key: '',
            routes: '',
            permission_type: null,
            title: '',
            parent: null,
            sort_order: '',
            icon: '',
            sub_title: '',
            category: null
        })
        setErrors({})
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{"Create"} Permission</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionTitle")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permissionTitle")}
                                        value={createUpdatePermissionFormData?.title}
                                        name="title"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                {errors?.title && (
                                    <CustomError text={errors.title} />
                                )}
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionSubTitle")} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permissionSubTitle")}
                                        value={createUpdatePermissionFormData?.sub_title}
                                        name="sub_title"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                            </div>

                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.routeKey")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.routeKey")}
                                        value={createUpdatePermissionFormData?.route_key}
                                        name="route_key"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.route_key && (
                                        <CustomError text={errors.route_key} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.routes")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.routes")}
                                        value={createUpdatePermissionFormData?.routes}
                                        name="routes"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                {errors?.routes && (
                                    <CustomError text={errors.routes} />
                                )}
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionType")} required={true} />
                                    <CustomSelect
                                        data={permissionConstantData?.permissionType}
                                        placeholder={t("placeholders.permissionType")}
                                        value={createUpdatePermissionFormData?.permission_type || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "permission_type")}
                                        name="permission_type"
                                    />
                                </div>
                                {errors?.permission_type && (
                                    <CustomError text={errors.permission_type} />
                                )}
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionParent")} />
                                    <CustomSelect
                                        data={menuPermissionData}
                                        placeholder={t("placeholders.permissionParent")}
                                        value={createUpdatePermissionFormData?.parent || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "parent")}
                                        name="parent"
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionSortOrder")} />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.permissionSortOrder")}
                                        value={createUpdatePermissionFormData?.sort_order}
                                        name="sort_order"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionIcon")} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permissionIcon")}
                                        value={createUpdatePermissionFormData?.icon}
                                        name="icon"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.permissionCategory")} />
                                    <CustomSelect
                                        data={permissionConstantData?.category}
                                        placeholder={t("placeholders.permissionType")}
                                        value={createUpdatePermissionFormData?.category || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "category")}
                                        name="category"
                                    />
                                </div>
                            </div>
                        </div>



                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdatePermission()}
                            isLoading={createPermissionPending}
                        />
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateUpdatePermission
