import React, { useEffect, useState } from 'react'
import AllPermissions from './AllPermissions'
import PermissionFilters from './PermissionFilters'
import { useDispatch, useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../translation'
import { employeeActions, permissionActions, rolesActions } from '../../../core/resource/actions'
import { CustomButton } from '../../globalComponents'
import CreateUpdatePermission from './CreateUpdatePermission'

const Permissions = () => {
    const [categoryWisePermissionData, setCategoryWisePermissionData] = useState('')
    const [isShowPermission, setIsShowPermission] = useState(false)
    const [allRolesData, setAllRolesData] = useState('')
    const [usersOnRoleData, setUsersOnRoleData] = useState('')
    const [permissionConstantData, setPermissionConstantData] = useState('')
    const [menuPermissionData, setMenuPermissionData] = useState('')
    const [roleOrUserWisePermissionData, setRoleOrUserWisePermissionData] = useState('')
    const [filters, setFilters] = useState({
        role: null,
        user: null
    })
    const dispatch = useDispatch()
    const { t } = useCustomTranslation()
    const {
        getPermissionConstantPending,
        getPermissionConstantRes,
        getPermissionConstantError,

        getMenuPermissionPending,
        getMenuPermissionRes,
        getMenuPermissionError,

        getCategoryWisePermissionPending,
        getCategoryWisePermissionRes,
        getCategoryWisePermissionError,

        createPermissionPending,
        createPermissionRes,
        createPermissionError,

        updatePermissionPending,
        updatePermissionRes,
        updatePermissionError,

        getRoleOrUserWisePermissionPending,
        getRoleOrUserWisePermissionRes,
        getRoleOrUserWisePermissionError,

    } = useSelector(state => state.permission)
    const {
        getAllRolesPending,
        getAllRolesRes,
        getAllRolesError,
    } = useSelector(state => state.roles)
    const {
        getUserOnRolePending,
        getUserOnRoleRes,
        getUserOnRoleError,
    } = useSelector(state => state.employee)

    useEffect(() => {
        getCategoryWisePermission()
        getAllRoles()
        getPermissionConstant()
        getMenuPermission()
        dispatch(permissionActions.handleClearPermissionStore("HANDLE_GET_ROLE_OR_USER_WISE_PERMISSION_STORE"))
    }, [])

    /* Get Category Wise Permission*/
    const getCategoryWisePermission = () => {
        dispatch(permissionActions.getCategoryWisePermissionRequest())
    }

    useEffect(() => {
        if (!getCategoryWisePermissionPending) {
            if (getCategoryWisePermissionError) {
                return;
            }
            if (getCategoryWisePermissionRes) {
                if (getCategoryWisePermissionRes?.code === 200) {
                    setCategoryWisePermissionData(getCategoryWisePermissionRes.data)
                }
            }
        }
    }, [
        getCategoryWisePermissionRes,
        getCategoryWisePermissionError,
        getCategoryWisePermissionPending
    ]);


    /* Create Update Permission */
    const toggleCreateUpdatePermissionModal = () => {
        setIsShowPermission(!isShowPermission)
    }

    const handleCreateUpdatePermission = (params) => {
        dispatch(permissionActions.createPermissionRequest(params))
    }

    useEffect(() => {
        if (!createPermissionPending) {
            if (createPermissionError) {
                dispatch(permissionActions.handleClearPermissionStore("HANDLE_CREATE_PERMISSION_STORE"))
                return;
            }
            if (createPermissionRes) {
                if (createPermissionRes?.code === 200) {
                    toggleCreateUpdatePermissionModal()
                    getCategoryWisePermission()
                    dispatch(permissionActions.handleClearPermissionStore("HANDLE_CREATE_PERMISSION_STORE"))
                }
            }
        }
    }, [
        createPermissionRes,
        createPermissionError,
        createPermissionPending
    ]);

    /* Get All Roles  */
    const getAllRoles = () => {
        dispatch(rolesActions.getAllRolesRequest())
    }
    useEffect(() => {
        if (!getAllRolesPending) {
            if (getAllRolesError) {
                return;
            }
            if (getAllRolesRes) {
                if (getAllRolesRes?.code === 200) {
                    setAllRolesData(getAllRolesRes?.data)
                }
            }
        }
    }, [
        getAllRolesRes,
        getAllRolesError,
        getAllRolesPending
    ]);

    /* Get User On Role  */
    const getUserOnRole = (roleId) => {
        const payload = {
            role_id: roleId
        }
        dispatch(employeeActions.getUserOnRoleRequest(payload))
    }
    useEffect(() => {
        if (!getUserOnRolePending) {
            if (getUserOnRoleError) {
                return;
            }
            if (getUserOnRoleRes) {
                if (getUserOnRoleRes?.code === 200) {
                    setUsersOnRoleData(getUserOnRoleRes?.data)
                }
            }
        }
    }, [
        getUserOnRoleRes,
        getUserOnRoleError,
        getUserOnRolePending
    ]);


    /* Get Permission Constant */
    const getPermissionConstant = (roleId) => {
        dispatch(permissionActions.getPermissionConstantRequest())
    }
    useEffect(() => {
        if (!getPermissionConstantPending) {
            if (getPermissionConstantError) {
                return;
            }
            if (getPermissionConstantRes) {
                if (getPermissionConstantRes?.code === 200) {
                    setPermissionConstantData(getPermissionConstantRes?.data)
                }
            }
        }
    }, [
        getPermissionConstantRes,
        getPermissionConstantError,
        getPermissionConstantPending
    ]);

    /* Get Menu Permission */
    const getMenuPermission = (roleId) => {
        dispatch(permissionActions.getMenuPermissionRequest())
    }
    useEffect(() => {
        if (!getMenuPermissionPending) {
            if (getMenuPermissionError) {
                return;
            }
            if (getMenuPermissionRes) {
                if (getMenuPermissionRes?.code === 200) {
                    setMenuPermissionData(getMenuPermissionRes?.data)
                }
            }
        }
    }, [
        getMenuPermissionRes,
        getMenuPermissionError,
        getMenuPermissionPending
    ]);

    /* Update Permission */
    const updatePermissions = (permissionIds) => {
        const params = {
            user_id: filters?.user?.value || '',
            role_id: filters?.role?.value || '',
            permission_ids: Array.from(permissionIds) || []
        }
        dispatch(permissionActions.updatePermissionRequest(params))
    }
    useEffect(() => {
        if (!updatePermissionPending) {
            if (updatePermissionError) {
                dispatch(permissionActions.handleClearPermissionStore("HANDLE_UPDATE_PERMISSION_STORE"))
                dispatch(permissionActions.handleClearPermissionStore("HANDLE_GET_ROLE_OR_USER_WISE_PERMISSION_STORE"))
                return;
            }
            if (updatePermissionRes) {
                if (updatePermissionRes?.code === 200) {
                    dispatch(permissionActions.handleClearPermissionStore("HANDLE_UPDATE_PERMISSION_STORE"))
                    dispatch(permissionActions.handleClearPermissionStore("HANDLE_GET_ROLE_OR_USER_WISE_PERMISSION_STORE"))
                    // setFilters({
                    //     role:null,
                    //     user:null
                    // })
                    getCategoryWisePermission()
                }
            }
        }
    }, [
        updatePermissionRes,
        updatePermissionError,
        updatePermissionPending
    ]);

    /* Get  Role Or User Wise Permission */
    const getRoleOrUserWisePermission = (prams) => {
        dispatch(permissionActions.getRoleOrUserWisePermissionRequest(prams))
    }
    useEffect(() => {
        if (!getRoleOrUserWisePermissionPending) {
            if (getRoleOrUserWisePermissionError) {
                return;
            }
            if (getRoleOrUserWisePermissionRes) {
                if (getRoleOrUserWisePermissionRes?.code === 200) {
                    setRoleOrUserWisePermissionData(getRoleOrUserWisePermissionRes?.data)
                }
            }
        }
    }, [
        getRoleOrUserWisePermissionRes,
        getRoleOrUserWisePermissionError,
        getRoleOrUserWisePermissionPending
    ]);


    return (
        <>
            <div className="d-flex  justify-content-end mb-3">
                <CustomButton
                    classNames={"btn btn-primary gradientBg "}
                    handleClick={() => toggleCreateUpdatePermissionModal()}
                    text={t("btns.createPermission")}
                />
            </div>
            <div className="cardBox myRequest">
                <div className="cardHead">
                    <div className="d-flex align-items-center">
                        <img
                            src="images/team-rating.svg"
                            alt=""
                            width={40}
                            className="me-2 fw-bold"
                        />
                        <div className="">
                            <h3> {t("headings.assignPermission")}</h3>
                            {/* <p>Lorem Ipsum is simply dummy text of the printing</p> */}
                        </div>

                    </div>

                </div>
                <PermissionFilters
                    allRolesData={allRolesData}
                    getUserOnRole={getUserOnRole}
                    usersOnRoleData={usersOnRoleData}
                    getRoleOrUserWisePermission={getRoleOrUserWisePermission}
                    filters={filters}
                    setFilters={setFilters}
                />
                <AllPermissions
                    categoryWisePermissionData={categoryWisePermissionData}
                    roleOrUserWisePermissionData={roleOrUserWisePermissionData}
                    updatePermissions={updatePermissions}
                />
                <CreateUpdatePermission
                    openModel={isShowPermission}
                    handleOpenClose={toggleCreateUpdatePermissionModal}
                    handleCreateUpdatePermission={handleCreateUpdatePermission}
                    permissionConstantData={permissionConstantData}
                    menuPermissionData={menuPermissionData}

                />

            </div>
        </>
    )
}

export default Permissions
