import React, { useEffect, useMemo, useState } from 'react'
import { CustCheckbox, CustomError } from '../../commanComponent'
import { CustomTable } from '../../reuseableComponents'
import CustomTableWithSubTable from '../../reuseableComponents/customTable/CustomTableWithSubTable'
import { CustomButton, CustomSpinner } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
import { useSelector } from 'react-redux'
import { toTitleCase } from '../../../utils/common'
import BlankTableError from '../../commanComponent/BlankTableError'
const AllPermissions = (props) => {
    const { t } = useCustomTranslation()
    const { categoryWisePermissionData, roleOrUserWisePermissionData, updatePermissions } = props
    const { getCategoryWisePermissionPending } = useSelector(state => state.permission)
    const [permissionIds, setPermissionIds] = useState(new Set([]))

    useEffect(() => {
        if (roleOrUserWisePermissionData) {
            const { permission_ids } = roleOrUserWisePermissionData
            setPermissionIds(new Set(permission_ids));
        }
    }, [roleOrUserWisePermissionData])

    /* Routes Permission Code*/
    const routesField = [
        { key: 'sn', label: 'S.No' },
        { key: 'permission', label: 'Permission' },
        { key: 'title', label: 'Title' },
        { key: 'permission_type', label: 'Permission Type' },
    ];
    const handlePermissionChecked = (e, item) => {
        let tempPermission = new Set(permissionIds)
        if (e.target.checked) {
            tempPermission.add(item?._id)
        } else {
            tempPermission.delete(item?._id)
        }
        setPermissionIds(tempPermission)

    }
    // useEffect(()=>{
    // if(getCategoryWisePermissionPending){
    //     setPermissionIds(new Set([]))
    // }
    // },[getCategoryWisePermissionPending])

    const renderCategoryWisePermissionData = () => {
        return (
            categoryWisePermissionData ? categoryWisePermissionData?.map((permissionData, index) => <div className='m-3' key={index}>
                <div className="">
                    <h5>{toTitleCase(permissionData?.distinct_category)}</h5>
                </div>
                <CustomTable
                    data={permissionData?.items}
                    fields={routesField}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={{
                        sn: (_, index) => (
                            <span>{index + 1}</span>
                        ),
                        permission: (item, index) => (
                            <span>
                                <CustCheckbox
                                    key={"is_active"}
                                    id={`is_active`}
                                    // label={"Active"}
                                    checked={permissionIds.has(item?._id)}
                                    handleChange={(e) => handlePermissionChecked(e, item)}
                                    name={"is_active"}
                                /></span>
                        ),


                    }}
                />
                <div className='d-flex justify-content-end'>
                    <CustomButton
                        text={t("btns.update")}
                        classNames="btn btn-primary"
                        handleClick={() => {
                            updatePermissions(permissionIds);
                        }}
                    />
                </div>
                <hr />
            </div>
            ) : <p><CustomError /></p>

        )
    }
    return (
        <div className='mt-3'>
            <div className="">
                <h5>All Permissions</h5>
            </div>
            {/* Menu Permission */}

            <hr />
            {getCategoryWisePermissionPending ? <CustomSpinner isShow={getCategoryWisePermissionPending} size="md" /> : renderCategoryWisePermissionData()}
            {/* {console.log(categoryWisePermissionData,"categoryWisePermissionData")} */}

            <hr />

        </div>
    )
}

export default AllPermissions
