import React from 'react';
import PropTypes from 'prop-types';
import TableDataHeader from './TableDataHeader';
import { CustomButton, CustomSpinner } from '../../globalComponents';
import TableName from './TableName';
import BlankTableError from '../../commanComponent/BlankTableError';
import CustomTd from './CustomTd';
import CustomPagination from '../../commanComponent/CustomPagination';
import { getCustomTableSN } from '../../../utils/common';

const CustomTableWithSubTable = ({ data,
    fields,
    isLoading,
    scopedSlots = {},
    onSearch,
    pagination,
    setCurrentPage,
    totalPages,
    currentPage,
    tableName,
    isHeader = true,
    tableStyle,
    isShowSubMenu
}) => {
    const renderTableBody = () => (
        data && data.length > 0 ? (
            data.map((item, index) => {
                const indexValue = getCustomTableSN(index, currentPage); // Get serial number

                return (
                    <React.Fragment key={index}>
                        {/* Main Row */}
                        <tr>
                            {fields.map((field) => (
                                field.key !== "sub_table" ? <CustomTd key={field.key}
                                    style={{ width: field.width || 'auto' }}
                                >
                                    {scopedSlots[field.key] ? scopedSlots[field.key](item, indexValue) : item[field.key]}
                                </CustomTd> : <CustomTd key={field.key} style={{ width: field.width || 'auto' }} />
                            ))}
                        </tr>

                        {/* Submenu Row */}
                        {scopedSlots['sub_table'] && isShowSubMenu.includes(index) && (
                            <tr>
                                <td colSpan={fields.length} style={{ padding: '3px 20px', backgroundColor: '#f9f9f9' }}>
                                    {scopedSlots['sub_table'](item, indexValue)}
                                </td>
                            </tr>

                        )}
                    </React.Fragment>
                );
            })
        ) : (
            !isLoading && <BlankTableError totalField={fields?.length} />
        )
    );


    return (
        <div className="cardBox mb-3 mt-3" style={tableStyle || {}}>
            {isHeader && <div className="cardHead d-flex align-items-center justify-content-between">
                <TableName name={tableName} />

                <div className="d-flex">
                    <div className="ticketFilter">
                        <input
                            type="text"
                            className="form-control fs-7"
                            placeholder="Filter.."
                        // onChange={e => onSearch(e.target.value)}
                        />
                    </div>
                    <CustomButton
                        text="Search"
                        classNames="btn btn-primary p-1 ms-3"
                    />
                </div>
            </div>}
            <div className="table-responsive tableDiv">
                <table className="table">
                    <thead>
                        <TableDataHeader tableHeader={fields} />
                    </thead>
                    <tbody>
                        {isLoading ? <tr>
                            <td colSpan={fields?.length}>
                                <CustomSpinner isShow={isLoading} size="xl" />
                            </td>
                        </tr>
                            :
                            renderTableBody()}
                    </tbody>
                </table>
            </div>

            {/* Pagination Controls */}
            {(totalPages > 1 && pagination) && <CustomPagination
                currentPage={currentPage}
                totalPages={totalPages}
                onPageChange={(pageNumber) => setCurrentPage(pageNumber)}
            />}
        </div>
    )
}

export default CustomTableWithSubTable
