import React, { useState } from 'react'
import { CustCheckbox, Label } from '../../commanComponent'
import CustomSelect from '../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../translation'
const PermissionFilters = (props) => {
    const {t}=useCustomTranslation()
    const {allRolesData,getUserOnRole,usersOnRoleData,getRoleOrUserWisePermission,setFilters,filters}=props
    const [roleChecked, setRoleChecked] = useState(true)
    const [userChecked, setUserChecked] = useState(false)

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e?.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setFilters(prevState => ({
            ...prevState,
            [name]: type === 'checkbox' ? checked : value,
        }));

        if(fieldName === "role" && value ) {
            const params={
                role_id:value?.value
            }
            getRoleOrUserWisePermission(params)
            getUserOnRole(value?.value)
            setFilters(prevState => ({
                ...prevState,
                ["user"]: null,
            }));
        }
        if(fieldName === "user"){
            const params={
                role_id:filters?.role?.value,
                user_id:value?.value
            }
            getRoleOrUserWisePermission(params)
        }

    };
    return (<>
        <div className="row">
            <div className="col-md-4">
                <div className="form-group mb-3 ">
                    <CustCheckbox
                        key={"role"}
                        id={`role`}
                        label={t("labels.role")}
                        checked={roleChecked}
                        handleChange={(e) => setRoleChecked(e.target.checked)}
                        name={"role"}
                    />
                </div>
                {roleChecked &&<div className="form-group mb-3 ">
                    <div className="col-md-12 form-group mb-3">
                        <Label text={t("labels.role")}
                        />
                        <CustomSelect
                            data={allRolesData}
                            placeholder={t("placeholders.role")}
                            value={filters?.role }
                            handleChange={(selectedOption) => handleFromChange(selectedOption, "role")}
                            name="role"
                        />
                    </div>
                </div>}
            </div>
            <div className="col-md-4">
                <div className="form-group mb-3">
                    <CustCheckbox
                        key={"user"}
                        id={`user`}
                        label={t("labels.user")}
                        checked={userChecked}
                        handleChange={(e) => setUserChecked(e.target.checked)}
                        name={"user"}
                    />
                </div>
                {userChecked &&<div className="form-group mb-3 ">
                    <div className="col-md-12 form-group mb-3">
                        <Label text={"User"}
                        />
                        <CustomSelect
                            data={usersOnRoleData}
                            placeholder={t("placeholders.user")}
                            value={filters?.user || ''}
                            handleChange={(selectedOption) => handleFromChange(selectedOption, "user")}
                            name="user"
                        />
                    </div>
                </div>}
            </div>
        </div>
    </>
    )
}

export default PermissionFilters
